@charset "utf-8";html{color:#000;background:transparent;;overflow-y:scroll;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%}html *{outline:0;-webkit-text-size-adjust:none;-webkit-tap-highlight-color:rgba(0,0,0,0)}html,body{font-family:sans-serif}body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td,hr,button,article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{margin:0;padding:0}input,select,textarea{font-size:100%}table{border-collapse:collapse;border-spacing:0}fieldset,img{border:0}abbr,acronym{border:0;font-variant:normal}del{text-decoration:line-through}address,caption,cite,code,dfn,em,th,var{font-style:normal;font-weight:500}ol,ul{list-style:none}caption,th{text-align:left}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:500}q:before,q:after{content:''}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline}sup{top:-.5em}sub{bottom:-.25em}a:hover{text-decoration:none}ins,a{text-decoration:none}
::-webkit-input-placeholder { /* WebKit browsers */ 
color: #a0a0a0; 
} 
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
color: #a0a0a0; 
} 
::-moz-placeholder { /* Mozilla Firefox 19+ */ 
color: #a0a0a0; 
} 
:-ms-input-placeholder { /* Internet Explorer 10+ */ 
color: #a0a0a0; 
} 
.imgBox{ width: 100%; overflow: hidden; height: auto; }
.imgBox img{ width: 100%; height: auto; display: block; margin: 0; }
.phoneBox{ width: 100%; padding: 0.506667rem 0; text-align: center; background: #6894fb; color: #fff; font-size: 0.426667rem;}
.phoneBox p:nth-of-type(2){margin-top: 0.066667rem;}
body a{text-decoration: none;color: #fff;}
body a:hover{text-decoration: none;}
#app {
    background-color: #f3f3f3;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}

#app .weui-cells {
    padding: 0 0.4rem;
    font-size: 0.426667rem;
    margin-top: 0;
}

#app .weui-cell {
    padding: 0.52rem 0;
}

#app .weui-cell:before,
#app .vux-cell-box:before {
    left: 0;
}

body .scroller-component {
    height: 5.6rem;
}

body .scroller-item {
    text-align: center;
    font-size: 0.426667rem;
    height: 0.8rem;
    line-height: 0.8rem;
    color: #000;
}

body .scroller-mask {
    background-size: 100% 2.4rem;
}

body .scroller-indicator {
    width: 100%;
    height: 0.8rem;
    position: absolute;
    left: 0;
    top: 2.4rem;
    z-index: 3;
}

body .vux-popup-picker-header {
    height: 1.066667rem;
    line-height: 1.066667rem;
    font-size: 0.426667rem;
    color: #366cd9;
}

body .vux-popup-picker-header-menu {
    line-height: 1.066667rem;
}

body .weui-cell_access .weui-cell__ft:after {
    background: url('../assets/right-arrow.png') center no-repeat;
    -webkit-background-size: 100%;
    background-size: 100%;
    border: none;
    width: 0.186667rem;
    height: 0.36rem;
    margin-top: -0.173333rem;
    transform: inherit;
}

body .vux-popup-picker-placeholder,
body .vux-popup-picker-value {
    padding-right: 0.293333rem;
}

.vux-header .vux-header-left .left-arrow {
    height: 1.066667rem;
    top: 0.04rem;
    position: absolute;
    width: 1.173333rem;
    background: url('../assets/left-arrow.png') center no-repeat;
    -webkit-background-size: 0.24rem;
    background-position: 0.413333rem;
    background-size: 0.24rem;
    background-position: 0.413333rem;
}

.vux-header {
    position: absolute;
    width: 100%;
    z-index: 8;
    padding: 0.04rem 0;
    box-sizing: border-box;
    background-color: #fff;
    border-bottom:1px solid #D9D9D9;
}

.vux-header .vux-header-left {
    left: 0;
}

.vux-header .vux-header-title,
.vux-header h1 {
    margin: 0 1.173333rem;
    line-height: 1.066667rem;
    text-align: center;
    height: 1.066667rem;
    font-size: 0.48rem;
    font-weight: 400;
    width: auto;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #313131;
}

#app .vux-popup-picker-value,
#app .weui-input {
    color: #a0a0a0;
}
#app .vux-cell-primary{ flex: 1; -webkit-flex:1; }
#app .next {
    padding: 0.306667rem 0;
    margin: 1.146667rem 0.4rem 1.146667rem 0.4rem;
    border-radius: 0.133333rem;
    text-align: center;
    color: #fff;
    background: #366cd9;
    font-size: 0.453333rem;
}



